import { SharedDataCtx, TSDState } from '@miq/contextjs';
import Staff from '@miq/staffjs';
import { StaffViewNavLinks } from '@miq/staffjs/src/components';
import * as React from 'react';
import { Route, Routes } from 'react-router-dom';
import { TStoreSettings } from '../types';

const CategoryView = React.lazy(() => import('./views.Category'));
const ProductView = React.lazy(() => import('./views.Product'));
const HitListView = React.lazy(() => import('./views.HitList'));

const navLinks = [
  { label: 'Products', link: { to: './' } },
  { label: 'Categories', link: { to: 'categories' } },
  { label: 'Hits', link: { to: 'hits' } },
];

export default function InsightsRoutes() {
  const ctx = React.useContext<TSDState<{ shopy_settings: TStoreSettings }>>(SharedDataCtx);

  const storeSlug = ctx?.shopy_settings?.slug;
  if (!storeSlug) return null;

  return (
    <Staff.View title="Insights" back="../">
      <StaffViewNavLinks navLinks={navLinks} />

      <Routes>
        <Route path="hits/" element={<HitListView {...{ storeSlug }} />} />
        <Route path="categories/" element={<CategoryView {...{ storeSlug }} />} />
        <Route index element={<ProductView {...{ storeSlug }} />} />
      </Routes>
    </Staff.View>
  );
}

